import { GenericResponse } from "./../../../../core/models/generic-response-model/generic-response.model";
import { TotalAmountsModel } from "./../../../../core/models/totals.model";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { FormListComponent } from "@fragments";
import { OrPreparation, OrPreparationReference, Payment } from "@models";
import { ApiService, SweetAlertService } from "@services";
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from "@shared";
import { Report } from "app/core/models/report.model";
import { NgxSpinnerService } from "ngx-spinner";
import Swal from "sweetalert2";
import { DecimalPipe } from "@angular/common";

@Component({
  selector: "app-or-preparation",
  templateUrl: "./or-preparation.component.html",
  styleUrls: ["./or-preparation.component.scss"],
})
export class OrPreparationComponent implements OnInit {
  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<OrPreparation>;
  displayedColumns: string[] = [
    "officialReceiptNo",
    "typeOfMediumDescription",
    "officialReceiptReferences",
    "status",
    "deleteIcon",
  ];
  statusList = [...STATUSES];

  orPreparation = new OrPreparation();
  selectedCompanyId: number;
  selectedCompany: any;
  orPreparationReference = new OrPreparationReference();
  orPreparationList = [];
  typeOfMediumList = [];
  officialReceiptReferenceList = [];
  availableReferenceCodeList = [];
  companyList = [];
  report = new Report();
  orPreparationForm: FormGroup;
  totalAmts: Array<Payment> = new Array<Payment>();
  totalAmounts: TotalAmountsModel = new TotalAmountsModel();
  isGettingCompanies: boolean;
  isWitholding: boolean;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private spinner: NgxSpinnerService,
    private numberPipe: DecimalPipe
  ) {}

  ngOnInit() {
    this.getList();
    this.setFormValidator();
    this.getTypeOfMedium();
    this.getCompanyList();
    this.getAvailableReferenceCode();
  }

  getList(): void {
    this.getAvailableReferenceCode();
    this.apiService.findAll(ENDPOINTS.officialReceipt).subscribe((res: any) => {
      this.orPreparationList = res.responseData.data;

      this.orPreparationList = this.orPreparationList.map((mappedList) => ({
        ...mappedList,
        officialReceiptReferencesValue: mappedList.officialReceiptReferences.reduce(
          (previousValue, currentValue) => {
            if (previousValue) {
              return previousValue + ", " + currentValue?.reference;
            } else {
              return currentValue?.reference;
            }
          },
          ""
        ),
      }));

      this.dataSource = new MatTableDataSource<OrPreparation>(
        this.orPreparationList
      );
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  amountChanged() {
    // this.orPreparationForm
    //   .get("totalAmount")
    //   .setValue(
    //     this.numberPipe.transform(
    //       (+this.orPreparationForm.get("amount").value || 0) +
    //         this.totalAmounts.totalSales,
    //       ".2-2"
    //     )
    //   );
  }

  addReceiptReference() {
    console.log("prep => ", this.orPreparationReference);
    if (
      (this.orPreparation.voucherControlNumber &&
        this.officialReceiptReferenceList.length >= 6) ||
      this.officialReceiptReferenceList.length >= 9
    ) {
      this.sweetAlertService.customErrorMessage("Max input limit reached");
    } else {
      const isExisting = this.officialReceiptReferenceList.filter(
        (filteredOR) =>
          filteredOR.reference === this.orPreparationReference.reference.trim()
      );
      if (isExisting.length) {
        this.sweetAlertService.customErrorMessage(
          "Reference code already in the list"
        );
      } else {
        this.apiService
          .findByParam(
            ENDPOINTS.orPreparationRefCodeCheck,
            this.orPreparationReference.reference
          )
          .subscribe(
            (res) => {
              if (res?.responseData?.data) {
                let appForm = res?.responseData?.data[0];
                this.apiService
                  .findByParam(ENDPOINTS.paymentDetailsByAppformId, appForm.id)
                  .subscribe((form: GenericResponse<Payment>) => {
                    console.log("form : ", form);
                    let paymentDtls;
                    if (form) {
                      //  this.totalAmounts.totalAmountDue = Number(this.totalAmounts.totalAmountDue) + Number(form?.responseData?.data?.amountDue);
                      // this.totalAmounts.setTotalValues(form.responseData.data)

                      this.totalAmts.push(form.responseData.data);
                      this.totalAmounts.setTotalValues(this.totalAmts);

                      this.orPreparationForm
                        .get("totalAmount")
                        .setValue(
                          this.numberPipe.transform(
                            this.totalAmounts.totalSales,
                            ".2-2"
                          )
                        );
                    }
                    console.log("totals : ", this.totalAmounts);
                  });

                this.officialReceiptReferenceList.push(
                  this.orPreparationReference
                );
                this.orPreparationReference.amount = appForm.amount;
                // this.getAvailableReferenceCode(this.orPreparationReference?.reference);
                this.orPreparationReference = new OrPreparationReference();
                // console.log("data => ", this.officialReceiptReferenceList)
                this.officialReceiptReferenceList.filter((ref) => {
                  // let total = Number(this.orPreparationForm.controls.totalAmount.value) + Number(ref.amount);
                  // this.orPreparationForm.controls.totalAmount.setValue(total);
                });
              } else {
                this.sweetAlertService.customErrorMessage(res?.responseMessage);
              }
            },
            (err) => {
              this.sweetAlertService.error(err);
            }
          );
      }
    }
  }

  removeReferenceCode(obj) {
    this.officialReceiptReferenceList = this.officialReceiptReferenceList.filter(
      (filteredOR) => filteredOR.reference !== obj.reference.trim()
    );
    console.log("remove ref : ", obj);
    console.log("totalAmts", this.totalAmts);
    this.totalAmts = this.totalAmts.filter(
      (amts) => amts.applicationForm.referenceCode !== obj.reference.trim()
    );
    console.log("totalamts", this.totalAmts);
    this.totalAmounts.setTotalValues(this.totalAmts);
    console.log("total amounts : ", this.totalAmounts);
  }

  getAvailableReferenceCode(refCode = "") {
    if (refCode) {
      this.apiService
        .findByParam(ENDPOINTS.availableReferenceCode, refCode)
        .subscribe((res) => {
          console.log(res);
        });
    } else {
      this.apiService
        .findAll(ENDPOINTS.availableReferenceCode)
        .subscribe((res) => {
          console.log(res);
          this.availableReferenceCodeList = res;
        });
    }
  }

  saveOrUpdate(): void {
    this.orPreparation.id = this.orPreparation?.id ? this.orPreparation.id : 0;
    this.orPreparation.officialReceiptReferences = this.officialReceiptReferenceList;
    // this.orPreparation.setTotalAmounts(this.totalAmounts);
    // console.log(this.totalAmounts.)
    console.log("orPrep", this.orPreparation);
    this.apiService
      .findByParam(
        ENDPOINTS.orPreparationORNumberCheck,
        this.orPreparation.officialReceiptNo
      )
      .subscribe(
        (isExisting) => {
          if (isExisting.responseData.data && !this.orPreparation?.id) {
            this.sweetAlertService.customErrorMessage(
              isExisting.responseMessage
            );
          } else {
            // this.orPreparation.setTotalAmounts(this.totalAmounts);
            // console.log('orPreparation')
            this.apiService
              .save(`${ENDPOINTS.orPreparation}/submit`, {
                ...this.orPreparation,
                ...this.totalAmounts,
              })
              .subscribe(
                (res: any) => {
                  if (res !== undefined) {
                    this.sweetAlertService.success(res);
                  }
                },
                (err: any) => {
                  this.sweetAlertService.error(err);
                },
                () => {
                  this.resetFormValidator();
                  this.getList();
                }
              );
          }
        },
        (err) => this.sweetAlertService.error(err)
      );
  }

  cancel(): void {
    Swal.fire({
      text: `Are you sure you want to cancel this Official Receipt Preparation?`,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        this.orPreparation = this.formListComponent.elementObject;
        this.orPreparation.officialReceiptReferences = null;
        this.apiService
          .save(`${ENDPOINTS.orPreparation}/cancel`, this.orPreparation)
          .subscribe(
            (res: any) => {
              if (res !== undefined) {
                this.sweetAlertService.success(res);
              }
            },
            (err: any) => {
              this.sweetAlertService.error(err);
            },
            () => {
              this.resetFormValidator();
              this.getList();
            }
          );
      }
    });
  }

  getOrPreparation(): void {
    console.log("get:: =>");
    this.apiService
      .findById(ENDPOINTS.officialReceipt, this.formListComponent.idForUpdate)
      .subscribe((res: any) => {
        if (res) {
          this.orPreparation = res.responseData.data;
          console.log("OR PREP => ", res);
          this.totalAmounts.totalSales = this.orPreparation.totalSales;
          this.totalAmounts.totalAmountDue = this.orPreparation.totalAmountDue;
          this.totalAmounts.totalVat = this.orPreparation.totalVat;
          this.totalAmounts.totalWithholdingtax = this.orPreparation.totalWithholdingtax;

          const company = this.companyList.find(
            (company) => company.companyName === this.orPreparation.companyName
          );

          this.selectedCompanyId = company.id;
          this.selectedCompany = company;
          this.getAvailableReferenceCodeList();
          this.orPreparationForm
            .get("totalAmount")
            .setValue(this.totalAmounts.totalSales);
          this.officialReceiptReferenceList = this.orPreparation.officialReceiptReferences;
          this.officialReceiptReferenceList.filter((or) => {
            this.apiService
              .findByParam(ENDPOINTS.orPreparationRefCodeCheck, or.reference)
              .subscribe((paymentDtls) => {
                let appForm = paymentDtls?.responseData?.data[0];
                this.apiService
                  .findByParam(ENDPOINTS.paymentDetailsByAppformId, appForm.id)
                  .subscribe((payment) => {
                    this.totalAmts.push(payment?.responseData?.data);
                  });
              });
          });
        }
      });
  }

  getCompanyList() {
    this.isGettingCompanies = true;
    this.apiService.findAll(ENDPOINTS.activeCompanies).subscribe(
      (res: any) => {
        this.companyList = res.responseData.data; // .filter((company) => !this.notInSelection.includes(company.status));
        this.isGettingCompanies = false;
      },
      (err: any) => {
        this.isGettingCompanies = false;
      }
    );
  }

  setCompanyDetails(i) {
    this.selectedCompanyId = i.id;
    this.orPreparation.companyName = i.companyName;
    this.orPreparation.companyTin = i.companyTin;
    this.orPreparation.companyAddress =
      "[" + i.country.code + " - " + i.country.name + "] " + i.completeAddress;
    this.getAvailableReferenceCodeList();
  }

  setFormValidator(): void {
    this.orPreparationForm = this.formBuilder.group({
      officialReceiptNo: ["", Validators.required],
      typeOfMedium: ["", Validators.required],
      amount: [""],
      officialReceiptReference: [""],
      voucherControlNumber: [""],
      businessStyle: [""],
      fullPaymentOf: [""],
      controlNumber: [""],
      companyName: [""],
      companyTin: [""],
      companyAddress: [""],
      totalAmount: [""],
    });
  }

  resetFormValidator(): void {
    this.selectedCompanyId = null;
    this.totalAmounts = new TotalAmountsModel();
    this.orPreparationForm.reset();
    this.orPreparation = new OrPreparation();
    this.officialReceiptReferenceList = [];
    this.orPreparationForm.markAsUntouched();
    this.orPreparationForm.markAsPristine();
  }

  getTypeOfMedium() {
    this.typeOfMediumList = [];

    this.apiService.findAll(ENDPOINTS.typeOfMediumSingleMedia).subscribe(
      (singleMediaList: any) => {
        this.apiService
          .findAll(ENDPOINTS.typeOfMediumMultiMediaMoving)
          .subscribe((multimediaMovingList: any) => {
            this.apiService
              .findAll(ENDPOINTS.typeOfMediumMultiMediaMoving)
              .subscribe((multimediaStaticList: any) => {
                this.typeOfMediumList = [
                  ...singleMediaList.responseData.data,
                  ...multimediaMovingList.responseData.data,
                  ...multimediaStaticList.responseData.data,
                ];
                const key = "id";
                this.typeOfMediumList = [
                  ...new Map(
                    this.typeOfMediumList.map((item) => [item[key], item])
                  ).values(),
                ];
              });
          });
      },
      (err: any) => {
        console.log(err);
      }
    );
  }

  compareList(value: any, selected: any): boolean {
    if (value && selected) {
      return value.id === selected.id;
    }
  }

  printOr(orData): void {
    if (orData.id) {
      this.report.filename = "ASC_Receipt";
      this.report.format = "pdf";
      (this.report.module = "Receipt"),
        (this.report.params = { officialReceiptId: orData.id });
      this.apiService
        .printReport(ENDPOINTS.generateReports, this.report)
        .subscribe((data: any) => {
          const newBlob = new Blob([data], { type: "application/pdf" });
          const downloadURL = URL.createObjectURL(newBlob);
          window.open(downloadURL);
        });
    } else {
      this.sweetAlertService.customErrorMessage("Please select data first...");
    }
  }

  getAvailableReferenceCodeList() {
    if (
      this.selectedCompanyId &&
      this.orPreparation.typeOfMedium &&
      this.orPreparation.filedBirForm
    ) {
      this.apiService
        .findAll(
          ENDPOINTS.availableReferenceCodeByCompany +
            "/" +
            this.selectedCompanyId +
            "/type-of-medium/" +
            this.orPreparation.typeOfMedium.description +
            "/file-bir-form/" +
            this.orPreparation.filedBirForm.toString()
        )
        .subscribe((res) => {
          this.availableReferenceCodeList = res;
        });
    }
  }
}
