import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OrPreparationComponent } from './or-preparation.component';

const routes: Routes = [
  {
    path: '',
    component: OrPreparationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OrPreparationRoutingModule { }
